.header-wrapper {
    max-width: 1230px;
    margin: 0 auto;
    padding: 60px 0 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    nav {
        a {
            font-size: 20px;
            line-height: 1.35;
            border-bottom: 1px solid inherit;

            &:not(:first-child) {
                margin-left: 60px;
            }

            &.linkActive {
                color: rgba(229, 156, 156, 1);
                border-bottom: 1px solid rgba(229, 156, 156, 1);
            }

       
        }
    }
}