.result-search {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.4;
}

.input-wrapper {
    position: relative;
    margin: 41px 0 120px;

    img {
        position: absolute;
        left: 20px;
        top: 10px;
        z-index: 4;
        cursor: pointer;
    }



    .input {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 40px;
        border: 1px solid rgba(136, 136, 136, 1);
        border-radius: 20px;
        padding-left: 60px;
        font-weight: 600;
        font-size: 15px;
        line-height: 1.4;
    }
}

.result-wrapper {
    color: rgb(51,51,51);

    p {
        padding: 100px 0;
        text-align: center;
    }
}