.main-wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;

    .background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -2;
        background-color: black;
      
    }

    main{
        flex: 1 0 auto;
        padding: 90px 0;
    }

    footer {
        flex: 0 0 auto;
    }
}

.main-wrapper::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index:-1;
    background: url('../../assets/background-main.jpg') repeat;
    opacity: .2;
}