.categoriesItem {
    &:hover {
        cursor: pointer;
    }

    &:not(:nth-last-child(-n+2)) {
        margin-bottom: 20px;
    }

    .categories-card {
        display: flex;
        width: 509px;
        color: rgba(51, 51, 51, 1);

        .img-wrapper {
            border-radius: 20px;
            overflow: hidden;
            width: 185px;
            height: 229px;
            margin-right: 43px;

            .image{
                
                width: 100%;
                height: 100%;
            }
        }

        .name-film{
            font-size: 20px;
            font-weight: 600;
            line-height: 1.35;
            margin-bottom: 57px;
        }

        .genres-film {
            font-size: 16px;
            line-height: 1.4;
        }
    }
}

