.movie-card {
    width: 180px;
    color: rgba(51, 51, 51, 1);

    &:hover {
        cursor: pointer;
    }

    &:first-child{
        margin-bottom: 40px;
    }

    img {
        width: 100%;
        border-radius: 20px;
        overflow: hidden;
    }

    .name-film {
        font-size: 15px;
        font-weight: 600;
        margin: 20px 0 25px;
    }

    .date-film {
        margin-bottom: 10px;
    }

    .date-film, .genres-film{
        font-size: 13px;
    }
}