.movies-detail-wrapper {
    display: flex;
    color:rgba(51, 51, 51, 1);
    min-height: 556px;

    .detail-img {
        height: 339px;
        width: 229px;
        border-radius: 20px;
        overflow: hidden;
        margin-right: 23px;
    
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .content-wrapper {
        flex: 1 1 auto;

        .content-header {
            display: flex;
            justify-content: space-between;
            
            .name-film {
                color: rgba(229, 156, 156, 1);
                font-size: 28px;
                font-weight: 700;
                line-height: 1;
            }

            .rating {  
                font-size: 27px;
                font-weight: 800;
                line-height: 1.4;
                
                span {
                    margin-left: 13px;
                }
            }
            
        }

        .text {         
            font-size: 16px;
            font-weight: 700;
            line-height: 1.4;
        }

        .info {
            font-weight: 600;
            line-height: 1.3;
        }

    }

}
